import {
	OgeAlertMetricsPanelCtrl
}
from 'app/plugins/sdk';
import moment from 'moment';
import _ from 'lodash';
import './css/clock-panel.css!';
import $ from 'jquery';

var panelOptionDefaults = {
	metrics: {},
	includeVars: true,

	isShowTime: true,
	isShowUnit: false,

	alarmStyle: 'none',
	colors: ["rgba(50, 172, 45, 0.97)", "rgba(237, 129, 40, 0.89)",
		"rgba(245, 54, 54, 0.9)"
	],

	fontFamily: '微软雅黑',
	fontSize: '100%',
	fontStyle: 'normal',

	dataPrecise: 2,
};

export class TablePanelCtrl extends OgeAlertMetricsPanelCtrl {

	/** @ngInject */
	constructor($scope, $injector, $rootScope, backendSrv, linkSrv) {
		super($scope, $injector);
		$scope.searchDashboards = function(queryStr, callback) {
			this.backendSrv.search({
				query: queryStr
			}).then(function(hits) {
				var dashboards = _.map(hits, function(dash) {
					return dash.title;
				});
				callback(dashboards);
			});
		};
		this.backendSrv = backendSrv;
		this.linkSrv = linkSrv;

		_.defaults(this.panel, {
			options: {}
		}); //将panelDefaults属性附加到this.panel上
		_.defaults(this.panel.options, panelOptionDefaults);

		this.events.on('init-edit-mode', this.onInitEditMode.bind(this));
		this.events.on('data-received', this.onDataReceived.bind(this));
		this.events.on('render', this.onRender.bind(this));
	}

	onInitEditMode() {
		this.addEditorTab('Options',
			'public/plugins/oge_table_link/editor.html', 2);
	}

	onDataReceived(dataList) {
		var tmpMetrics = this.panel.options.metrics;
		this.panel.options.metrics = {};

		if (_.isEmpty(dataList)) return;

		for(var i = 0; i < dataList.length; i++) {
			var item = dataList[i];
			this.panel.options.metrics[item.refId] = tmpMetrics[item.refId] || {
				link: {
					type: 'none',
					url: null,
					dashboard: null,
					dashUri: null
				},
				unit: null,
				threshold: '',
			};
		}

		this.data = dataList;
		this.render();
	}

	onRender() {
		if (_.isEmpty(this.data)) return; // 数据为空，则跳出

		for (var i = 0; i < this.data.length; i++) {
			var dat = this.data[i];

			// 设置 Value 和 Time
			dat.value = '-';
			dat.time = '-';
			if (!_.isEmpty(dat.datapoints)) {
				var value = dat.datapoints[dat.datapoints.length-1][0].toFixed(this.panel.options.dataPrecise);
				if (value !== -9999) dat.value = value;

				var time = dat.datapoints[dat.datapoints.length-1][1];
				if (Number.parseInt(time) > 0)
					dat.time = moment(Number.parseInt(time)).format("YYYY-MM-DD HH:mm:ss");
			}
			if(this.panel.ogeAlertEnabled){
				if(dat.alarm == null){
					continue
				} else {
					dat.color = this.getAlertLevelColor(dat.alarm.level);
				}
			}
			
		}
	}

	link(scope, elem, attrs, ctrl) {

			var pageCount = 0;
			//table高度
			function getTableHeight() {
				var panelHeight = ctrl.height;
				if (pageCount > 1) {
					panelHeight -= 26;
				}
				return (panelHeight - 31) + 'px';
			}
		ctrl.events.on('render', function() {
			if(_.isEmpty(ctrl.data)) return;

			var rootElem = elem.find('.table-panel-scroll'); //表格需要处理高度
			pageCount = ctrl.data.length;
			rootElem.css({
				'max-height': getTableHeight()
			})
		});
	}

	formatLinkHref(link) {
		if (link.type === 'none') {
			return '###';
		}

		link.includeVars = this.panel.options.includeVars;
		var linkInfo = this.linkSrv.getPanelLinkAnchorInfo(link, this.panel.scopedVars);
		return linkInfo.href;
	}


	invertColorOrder() {
		this.panel.options.colors.reverse()
	}

	dashboardChanged(link) {
		this.backendSrv.search({
			query: link.dashboard
		}).then(function(hits) {
			var dashboard = _.find(hits, {
				title: link.dashboard
			});
			if (dashboard) {
				link.dashUri = dashboard.uri;
			}
		});
	}

	searchDashboards(queryStr, callback) {
		this.backendSrv.search({
			query: queryStr
		}).then(function(hits) {
			var dashboards = _.map(hits, function(dash) {
				return dash.title;
			});
			callback(dashboards);
		});
	}
}

TablePanelCtrl.templateUrl = 'module.html';
